.nav-container {
  display: flex;
  padding: 1.4rem 8.2rem;
  /* border-bottom: 1.6px solid #22b8cf; */
  background-color: rgba(34, 184, 207, 0.9);
}
.nav-links {
  list-style: none;
  display: flex;
  gap: 4.2rem;
  align-items: center;
}

.nav-links li a {
  text-decoration: none;
  font-size: 2.6rem;
  font-weight: 400;
  letter-spacing: 1.2px;
  color: #fff;
}

.nav-container img {
  height: 82px;
  width: 82px;
  margin-right: auto;
  border-radius: 12px;
  box-shadow: 0px 8px 36px rgba(0, 0, 0, 0.1);
}

.nav-link:hover {
  /* background-color: #f03e3e; */
  border-bottom: 6px solid #99e9f2;
  border-radius: 12px;
  padding: 0.5rem;
}

.call-us {
  display: flex;
  align-items: center;
  font-size: 2.3rem !important;
  font-weight: 500;
  color: #fff !important;
}
.phone-icon {
  /* color: white; */
  height: 24px;
  width: 24px;
  margin-right: 1.2rem;
  transform: rotate(10deg);
  /* color: black !important; */
}

.call-us a:hover {
  border-bottom: 6px solid #99e9f2;
  border-radius: 12px;
  padding: 0.5rem;
}

@media (min-width: 768px) {
  .nav-container {
    /* flex-direction: column; */
  }
}
